import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';

import { SaveService, CommonService } from '@app/core/http/serviceIndex';
import { Dictation } from '@app/shared/models/ListeningModel';


@Component({
  selector: 'dictation',
  templateUrl: './dictation.component.html',
  styleUrls: ['./dictation.component.css']
})
export class DictationComponent implements OnInit {

  createDictionForm: FormGroup = this.formBuilder.group({
    title: ["", [Validators.required]],
    duration: [0, [Validators.required]],
    answer: ["", [Validators.required]],
    audioFile:[null,Validators.required]
  });

  private questionCode: string;
  private questionId: string;

  public audioFile: File;
  public answerWarningText: string =
    '(WRITE THE SENTENCE EXACTLY YOU WANT FROM THE STUDENT)';
  public isErrText: string = 'You must upload audio file';

  private subscription: Subscription;
  // boolean
  public submitted: boolean = false;
  isToast: boolean = false;

  constructor(
    private _activeRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private _createService: SaveService,
  ) { }

  ngOnInit() {
    this.questionCode = this._activeRoute.snapshot.params['questionType'];

    let data = this._activeRoute.snapshot.data['questionDetail'];
    if (data)
      this.getDataEditForm(data);
  }


  getDataEditForm(data: Dictation) {
    this.questionId = data.questionId;
    this.createDictionForm = this.formBuilder.group({
      title: [data.title, [Validators.required]],
      duration: [data.duration, [Validators.required]],
      answer: [data.answer, [Validators.required]],
      audioFile:[null]
    });

  }

  get f() {
    return this.createDictionForm.controls;
  }

  getAudioFile(e) {
    this.audioFile = e;
  }

  goBack() {
    this._createService.goBackPrev();
  }

  onCreate() {
    this.submitted = true;
    if (this.createDictionForm.invalid) {
      return;
    } else {
      this.subscription = this._createService.onSaveDictation(this.createDictionForm.value, this.questionCode, this.questionId).subscribe(
        () => {
          this.goBack();
        });
    }
  }

  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }
}
