import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SaveService } from '@app/core/http/serviceIndex';
import { ShortQuestion } from '@app/shared/models/SpeakingModel';
import { Subscription } from 'rxjs';

@Component({
  selector: 'answer-short-question',
  templateUrl: './answer-short-question.component.html',
  styleUrls: ['./answer-short-question.component.css']
})
export class AnswerShortQuestionComponent implements OnInit {
  private questionCode: string;
  private questionId: string;

  public createansShortForm: FormGroup = this.formBuilder.group({
    title: ["", [Validators.required]],
    duration: ["", [Validators.required]],
    answer: ["", [Validators.required]],
    recordDuration: ["", [Validators.required]],
    audioFile: [null, Validators.required],
    contentScore: ["", Validators.required],
  });

  // boolean
  public submitted: boolean = false;
  public isProgress: boolean = false;
  isToast: boolean = false;
  public isCheckAudio: boolean = false;
  // any
  public editorData: any = "";
  public audioFile: any;
  isErrText: String;
  public exitaudioLink: String;

  public subscription: Subscription;

  constructor(private _activeRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private _createService: SaveService) { }

  ngOnInit() {
    this.questionCode = this._activeRoute.snapshot.params['questionType'];
    let data = this._activeRoute.snapshot.data['questionDetail'];
    if (data)
      this.getDataEditForm(data);
  }

  getDataEditForm(data: ShortQuestion) {
    this.questionId = data.questionId;
    this.createansShortForm = this.formBuilder.group({
      title: [data.title, [Validators.required]],
      duration: [data.duration, [Validators.required]],
      answer: [data.answer, [Validators.required]],
      recordDuration: [data.recordDuration, [Validators.required]],
      audioFile: [null],
      contentScore: [data.contentScore, Validators.required],

    });

  }

  onCreate() {
    this.submitted = true;

    if (this.createansShortForm.invalid) {
      return;
    } else {
      this.isProgress = true;

      this.subscription = this._createService.onSaveShortQuestion(
        this.createansShortForm.value, this.questionCode, this.questionId
      ).subscribe(
        (res) => {
          this.goBack();
        },
        (err) => {
          this.isErrText = err;
          this.isToast = true;
          this.isProgress = false;
        }
      );
    }
  }



  goBack() {
    this._createService.goBackPrev();
  }

  get f() {
    return this.createansShortForm.controls;
  }

  getAudioFile(e) {
    this.audioFile = e;
  }
  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }

}
