import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import {  SaveService } from '@app/core/http/serviceIndex';
import { MCq } from '@app/shared/models/ReadingModel';
import { Subscription } from 'rxjs';

@Component({
  selector: 'mcq-multiple',
  templateUrl: './mcq-multiple.component.html',
  styleUrls: ['./mcq-multiple.component.css']
})
export class McqMultipleComponent implements OnInit {
  private questionCode: string;
  private questionId:string;

  // boolean
  public isCreate: boolean = false;
  public submitted: boolean = false;

  mcqForm: FormGroup = this.formBuilder.group(
    {
      question:["",Validators.required],
      answers: ["", [Validators.required]],
      options: ["", [Validators.required]]
    }
  );

  // any
  public editorData: any = "";
  public isProgress: boolean = false;
  public type: any;
  public warmingTxt: any = "(SEPARETED BY ; EX: ANSWER;OPTION1;OPTION2;OPTION3 )";
  public subscription: Subscription;

  constructor(private _activeRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private _createService: SaveService) { }

  ngOnInit() {
    this.questionCode = this._activeRoute.snapshot.params['questionType'];

    let data = this._activeRoute.snapshot.data['questionDetail'];
    if (data)
      this.getDataEditForm(data);

  }

  getDataEditForm(data: MCq) {
    this.questionId = data.mcqId;
    this.mcqForm = this.formBuilder.group(
      {
        question:[data.question,Validators.required],
        answers: [data.answers, [Validators.required]],
        options: [data.options, [Validators.required]]
      }
    );
      this.editorData=data.question;
  }

  get f() {
    return this.mcqForm.controls;
  }

  getMarkDown(e) {
    this.editorData = e;
    this.mcqForm.get('question').setValue(e.innerHTML);
  }

  goBack() {
    this._createService.goBackPrev();
  }

  onCreate() {
    this.submitted = true;
    if (this.mcqForm.invalid && !this.editorData.innerHTML) {
      return;
    } else {

      this.isProgress = true;
   

      this.subscription = this._createService.onSaveMCQ(this.mcqForm.value, this.questionCode,this.questionId).subscribe(
        (res) => {
          this.isProgress = false;
          this.goBack();
        })
    }
  }

  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }

}
