import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SaveService } from '@app/core/http/serviceIndex';
import { RepeatSentence } from '@app/shared/models/SpeakingModel';
import { Subscription } from 'rxjs';

@Component({
  selector: 'repeat-sentence',
  templateUrl: './repeat-sentence.component.html',
  styleUrls: ['./repeat-sentence.component.css']
})
export class RepeatSentenceComponent implements OnInit {
  private questionCode: string
  private questionId: string

  public createRepeatSentenceForm: FormGroup = this.formBuilder.group({
    title: ["", [Validators.required]],
    paragraph: ["", [Validators.required]],
    countMax: ["", [Validators.required]],
    duration: ["", [Validators.required]],
    recordDuration: ["", [Validators.required]],
    fluencyGrace: ["", [Validators.required]],
    pronunciationGrace: ["", [Validators.required]],
    words: this.formBuilder.array([]),
    audioFile: [null, [Validators.required]],
    contentScore: ["", [Validators.required]]
  });

  // boolean
  public submitted: boolean = false;
  public isProgress: boolean = false;
  public isToast: boolean = false;
  //any
  public words: any = [];
  public repeatSentenceId: any
  public isErrText: String = null
  public subscription: Subscription
  public audioFile: File = null

  constructor(
    private _activeRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private _createService: SaveService,) { }

  ngOnInit() {
    this.questionCode = this._activeRoute.snapshot.params['questionType'];

    let data = this._activeRoute.snapshot.data['questionDetail'];
    if (data)
      this.getDataEditForm(data);
  }

  getDataEditForm(data: RepeatSentence) {
    this.questionId = data.questionId;
    this.createRepeatSentenceForm = this.formBuilder.group({
      title: [data.title, [Validators.required]],
      paragraph: [data.paragraph, [Validators.required]],
      countMax: [data.countMax, [Validators.required]],
      duration: [data.duration, [Validators.required]],
      recordDuration: [data.recordDuration, [Validators.required]],
      fluencyGrace: [data.fluencyGrace, [Validators.required]],
      pronunciationGrace: [data.pronunciationGrace, [Validators.required]],
      words: this.formBuilder.array([]),
      audioFile: [null],
      contentScore: [data.contentScore, [Validators.required]]

    });
    this.words = data.words.map(d => ({ word: d.word }))
  }


  onSave() {
    this.submitted = true;
    if (this.createRepeatSentenceForm.invalid)
      return

    this.isProgress = true

    this.subscription = this._createService.onSaveRepeatSentence(
      this.createRepeatSentenceForm.value,
      this.questionCode, this.questionId
    ).subscribe(
      (res) => {
        this.goBack()
      },
      (err) => {
        this.isErrText = err.error.message;
        this.isToast = true;
        this.isProgress = false;
      }
    )

  }

  getAudioFile(e) {
    this.createRepeatSentenceForm.value.audioFile = e;
  }

  get f() {
    return this.createRepeatSentenceForm.controls;
  }

  goBack() {
    this._createService.goBackPrev();
  }

  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }

}
