import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SaveService } from '@app/core/http/serviceIndex';
import { DescribeImage } from '@app/shared/models/SpeakingModel';
import { Subscription } from 'rxjs';

@Component({
  selector: 'describe-image',
  templateUrl: './describe-image.component.html',
  styleUrls: ['./describe-image.component.css']
})
export class DescribeImageComponent implements OnInit {
  public questionCode: string;
  public questionId: string;

  createDescribeForm: FormGroup = this.formBuilder.group({
    title: ["", [Validators.required]],
    duration: ["", [Validators.required]],
    recordDuration: ["", [Validators.required]],
    keywordMatch: ["", [Validators.required]],
    keywords: ["", [Validators.required]],
    fluentGrade: ["", [Validators.required]],
    sampleAnswer: ["", [Validators.required]],
    contentScore: ["", [Validators.required]],
    pronounciationGrade: ["", [Validators.required]],
    words: this.formBuilder.array([]),
    imageFile: [null, Validators.required]
  });

  // boolean
  public submitted: boolean = false;
  public isProgress: boolean = false;
  public isCheckImg: boolean = false;
  isToast: boolean = false;
  public isImgLoad: boolean = true;

  // any
  public editorData: any = "";
  isErrText: string;
  public exitimgUrl: string;
  public subscription: Subscription;
  public words: any = [];

  constructor(private _activeRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private _createService: SaveService) { }

  ngOnInit() {
    this.questionCode = this._activeRoute.snapshot.params['questionType'];

    let data = this._activeRoute.snapshot.data['questionDetail'];
    if (data)
      this.getDataEditForm(data);
  }

  getDataEditForm(data: DescribeImage) {
    this.questionId = data.questionId;
    this.createDescribeForm = this.formBuilder.group({
      title: [data.title, [Validators.required]],
      duration: [data.duration, [Validators.required]],
      recordDuration: [data.recordDuration, [Validators.required]],
      keywordMatch: [data.keywordMatch, [Validators.required]],
      keywords: [data.keywords, [Validators.required]],
      fluentGrade: [data.fluentGrade, [Validators.required]],
      pronounciationGrade: [data.pronounciationGrade, [Validators.required]],
      words: this.formBuilder.array([]),
      sampleAnswer: [data.sampleAnswer, [Validators.required]],
      contentScore: [data.contentScore, [Validators.required]],


      imageFile: [null]
    });
    this.words = data.words.map(d => ({ word: d.word }))
  }

  onCreate() {
    this.submitted = true;

    if (this.createDescribeForm.invalid) {
      return;
    } else {
      this.isProgress = true;

      this.subscription = this._createService.onSaveDescribeImg(
        this.createDescribeForm.value, this.questionCode, this.questionId
      ).subscribe(
        (res) => {
          this.goBack();
        },
        (err) => {
          this.isErrText = err;
          this.isToast = true;
          this.isProgress = false;
        }
      );
    }
  }


  goBack() {
    this._createService.goBackPrev();
  }

  get f() {
    return this.createDescribeForm.controls;
  }




  onLoad() {
    this.isImgLoad = false;
  }
  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }

}
