import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SaveService } from '@app/core/http/serviceIndex';
import { HighlightCorrectSummary } from '@app/shared/models/ListeningModel';
import { Subscription } from 'rxjs';

@Component({
  selector: 'highlight-correct-summary',
  templateUrl: './highlight-correct-summary.component.html',
  styleUrls: ['./highlight-correct-summary.component.css']
})
export class HighlightCorrectSummaryComponent implements OnInit {
  private questionCode: string;
  private questionId: string;
  // boolean
  public submitted: boolean = false;
  public isToast: boolean = false

  public createCorrectSummaryForm: FormGroup = this.formBuilder.group(
    {
      title: ["", [Validators.required]],
      duration: ["", [Validators.required]],
      question: ["", [Validators.required]],
      answer: ["", [Validators.required]],
      options: ["", [Validators.required]],
      audioFile: [null, [Validators.required]]
    }
  );

  // any
  public answerWarningText: String = "(GIVE INCORRECT WORDS IN {} IN EX;\"THIS IS A {WORDPARAGRAPH}.IT HAS...\"";
  public isErrText: string = 'You must upload audio file!';
  private subscription: Subscription;

  constructor(private _activeRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private _createService: SaveService) {

  }

  ngOnInit() {
    this.questionCode = this._activeRoute.snapshot.params['questionType'];

    let data = this._activeRoute.snapshot.data['questionDetail'];
    if (data)
      this.getDataEditForm(data);

  }

  getDataEditForm(data: HighlightCorrectSummary) {
    this.questionId = data.questionId;
    this.createCorrectSummaryForm = this.formBuilder.group(
      {
        title: [data.title, [Validators.required]],
        duration: [data.duration, [Validators.required]],
        question: [data.question, [Validators.required]],
        answer: [data.answer, [Validators.required]],
        options: [data.options, [Validators.required]],
        audioFile: [null]
      }
    );

  }

  get f() {
    return this.createCorrectSummaryForm.controls;
  }
  
  goBack() {
    this._createService.goBackPrev();
  }

  onCreate() {
    this.submitted = true;
    if (this.createCorrectSummaryForm.invalid) {
      return
    }
    this.subscription = this._createService.onSaveHighlightCorrectSummary(
      this.createCorrectSummaryForm.value, this.questionCode, this.questionId
    ).subscribe(
      () => {
        this.goBack();
      })

  }

  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }

}
