import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SaveService } from '@app/core/http/save.service';
import { SummaryListening } from '@app/shared/models/ListeningModel';
import { Subscription } from 'rxjs';

@Component({
  selector: 'summary-spoken-text',
  templateUrl: './summary-spoken-text.component.html',
  styleUrls: ['./summary-spoken-text.component.css'],
  
})
export class SummarySpokenTextComponent implements OnInit {

  createSummary: FormGroup = this.formBuilder.group({
    name: ["", [Validators.required]],
    type: ["", [Validators.required]],
    question: ["", [Validators.required]],
    contentKeywords: ["", [Validators.required]],
    vocabularyWords: ["", [Validators.required]],
    maxContentKeywords: ["", [Validators.required]],
    adviseContent: ["", [Validators.required]],
    maxWordLimit: ["", [Validators.required]],
    audioFile: [null, Validators.required]
  });

  // any
  isErrText: String = null;
  public summaryId: any;
  public editorDataQestion: any = "";
  public editorDataAdvise: any = "";
  // boolean
  public submitted: boolean = false;
  public isProgress: boolean = false;
  public isTouchQuestion: boolean = false;
  public isTouchAdvise: boolean = false;
  isToast: boolean = false;
  public subscription: Subscription;
  public questionCode: string;

  public typeList: any = [
    {
      _id: "SPOKEN",
      name: "SPOKEN",
    },
  ];
  constructor(
    private _activeRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private dataService: SaveService,
  ) { }

  ngOnInit() {
    this.questionCode = this._activeRoute.snapshot.params['questionType'];
    let data = this._activeRoute.snapshot.data['questionDetail'];
    if (data)
      this.getDataEditForm(data);
  }

  getDataEditForm(data:SummaryListening){
    this.summaryId = data.questionId;
    this.createSummary=this.formBuilder.group({
      name: [data.name, [Validators.required]],
      type: [data.type, [Validators.required]],
      question: [data.question, [Validators.required]],
      contentKeywords: [data.contentKeywords, [Validators.required]],
      vocabularyWords: [data.vocabularyWords, [Validators.required]],
      maxContentKeywords: [data.maxContentKeywords, [Validators.required]],
      adviseContent: [data.adviseContent, [Validators.required]],
      maxWordLimit: [data.maxWordLimit, [Validators.required]],
      audioFile: [null]
    });
    this.editorDataQestion=data.question;
    this.editorDataAdvise=data.adviseContent;
  }

  get f() {
    return this.createSummary.controls;
  }
  getMarkDownForSummaryQuestion(e) {
    this.editorDataQestion = e;
    this.isTouchQuestion = true;
    this.createSummary.get('question').setValue(e.innerHTML);
  }
  getMarkDownForAdviseSummary(e) {
    this.editorDataAdvise = e;
    this.isTouchAdvise = true;
    this.createSummary.get('adviseContent').setValue(e.innerHTML);

  }
  onCreate() {
    this.submitted = true;
    if (this.createSummary.invalid) {
      this.isProgress = false;
      return;
    } else {
      this.subscription = this.dataService.OnSaveSummarySpokenText(this.createSummary.value, this.questionCode, this.summaryId).subscribe(
        () => {
          this.goBack()
        });
    }
  }

  goBack() {
    this.dataService.goBackPrev();
  }
  


  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }
}
