import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { QuestionDetailResolver } from "@app/modules/manage/manage-questions/reslovers/question-detail.reslover";
import { QuestionCreateComponent } from "@app/modules/manage/manage-questions/question-create/question-create/question-create.component";





const routes: Routes = [
  {
    path: "",
    component: QuestionCreateComponent,
  },
  {
    path: ":questionId",
    component: QuestionCreateComponent,
    resolve: {
      questionDetail: QuestionDetailResolver
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  providers: [
    QuestionDetailResolver
  ]
})

export class QuestionCreateRoutingModule { }