import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { CommonService, SaveService } from '@app/core/http/serviceIndex';
import { RetellLecture } from '@app/shared/models/SpeakingModel';
import { Subscription } from 'rxjs';

@Component({
  selector: 'retell-lecture',
  templateUrl: './retell-lecture.component.html',
  styleUrls: ['./retell-lecture.component.css']
})
export class RetellLectureComponent implements OnInit {
  private questionCode: string;
  private questionId: string;

  public createRetellLectureForm: FormGroup = this.formBuilder.group({
    title: ["", [Validators.required]],
    keywords: ["", [Validators.required]],
    countMax: ["", [Validators.required]],
    duration: ["", [Validators.required]],
    recordDuration: ["", [Validators.required]],
    fluencyGrace: ["", [Validators.required]],
    pronunciationGrace: ["", [Validators.required]],
    contentScore: ["", [Validators.required]],
    // words: this.formBuilder.array([], [Validators.required]),
    words: ["", [Validators.required]],
    audioFile: [null, Validators.required]
  });
  // boolean
  public submitted: boolean = false;
  public isProgress: boolean = false;
  public isToast: boolean = false;
  //any
  public words: any = [];
  public retellLectureId: any
  public isErrText: String = null
  public subscription: Subscription

  constructor(
    private _activeRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private _createService: SaveService) { }

  ngOnInit() {
    this.questionCode = this._activeRoute.snapshot.params['questionType'];

    let data = this._activeRoute.snapshot.data['questionDetail'];
    if (data)
      this.getDataEditForm(data);
  }

  getDataEditForm(data: RetellLecture) {
    this.questionId = data.questionId;
    this.createRetellLectureForm = this.formBuilder.group({
      title: [data.title, [Validators.required]],
      keywords: [data.keywords, [Validators.required]],
      countMax: [data.countMax, [Validators.required]],
      duration: [data.duration, [Validators.required]],
      recordDuration: [data.recordDuration, [Validators.required]],
      fluencyGrace: [data.fluencyGrace, [Validators.required]],
      pronunciationGrace: [data.pronunciationGrace, [Validators.required]],
      words: [data.words, [Validators.required]],
      contentScore: [data.contentScore, [Validators.required]],

      audioFile: [null]
    });
    // this.words = data.words.map(d => ({ word: d.word }))
  }

  onSave() {
    this.submitted = true;
    if (this.createRetellLectureForm.invalid)
      return


    this.isProgress = true
    console.log(this.questionCode)
    this.subscription = this._createService.onSaveRellLecture(
      this.createRetellLectureForm.value, this.questionCode, this.questionId
    ).subscribe(
      (res) => {
        this.goBack()
      },
      (err) => {
        this.isErrText = err;
        this.isToast = true;
        this.isProgress = false;
      }
    )

  }



  get f() {
    return this.createRetellLectureForm.controls;
  }

  goBack() {
    this._createService.goBackPrev();
  }

  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }


}
