import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SaveService } from '@app/core/http/save.service';
import { SelectMissingWords } from '@app/shared/models/ListeningModel';
import { Subscription } from 'rxjs';

@Component({
  selector: 'select-missing-words',
  templateUrl: './select-missing-words.component.html',
  styleUrls: ['./select-missing-words.component.css']
})
export class SelectMissingWordsComponent implements OnInit {
  private questionCode: string;
  private questionId:string;

  public creatMissingWordForm: FormGroup = this.formBuilder.group({
    title: ["", [Validators.required]],
    duration: ["", [Validators.required]],
    question: ["", [Validators.required]],
    answer: ["", [Validators.required]],
    option: ["", [Validators.required]],
    audioFile:[null,Validators.required]
  });

  isErrText: string = '';

  // boolean
  public submitted: boolean = false;
  isToast: boolean = false;

  public subscription: Subscription;

  constructor(
    private _activeRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private _createService: SaveService
  ) { }

  ngOnInit() {
    this.questionCode = this._activeRoute.snapshot.params['questionType'];

    let data = this._activeRoute.snapshot.data['questionDetail'];
    if (data)
      this.getDataEditForm(data);

  }

  getDataEditForm(data:SelectMissingWords){
    this.questionId = data.questionId;

    this.creatMissingWordForm=this.formBuilder.group({
      title: [data.title, [Validators.required]],
      duration: [data.duration, [Validators.required]],
      question: [data.question, [Validators.required]],
      answer: [data.answer, [Validators.required]],
      option: [data.options, [Validators.required]],
      audioFile:[null]
    });
  }
  get f() {
    return this.creatMissingWordForm.controls;
  }



  goBack() {
   this._createService.goBackPrev();
  }



  onCreate() {
    this.submitted = true;
    if (this.creatMissingWordForm.invalid) {
      return;
    } else {
      this.subscription = this._createService.OnSaveMissingWords(this.creatMissingWordForm.value, this.questionCode,this.questionId).subscribe(
        (res) => {
          this.goBack();
        });
    }
  }

  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }

}
