import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SaveService } from '@app/core/http/serviceIndex';
import { HighlightIncorrectWord } from '@app/shared/models/ListeningModel';
import { Subscription } from 'rxjs';

@Component({
  selector: 'highlight-incorrect-words',
  templateUrl: './highlight-incorrect-words.component.html',
  styleUrls: ['./highlight-incorrect-words.component.css']
})
export class HighlightIncorrectWordsComponent implements OnInit {
  private questionCode: string;
  private questionId:string;

  // boolean
  public submitted: boolean = false;
 public isToast: boolean = false;

  public createIncorrectWordForm: FormGroup = this.formBuilder.group(
    {
      title: ["", [Validators.required]],
      duration: ["", [Validators.required]],
      paragraph: ["", [Validators.required]],
      audioFile: [null, [Validators.required]],
    }
  );

  // any
  public answerWarningText: string = "(GIVE INCORRECT WORDS IN {} IN EX;\"THIS IS A {WORDPARAGRAPH}.IT HAS...\")";
  isErrText: string = 'You must upload audio file!';
  public subscription: Subscription;

  constructor(private _activeRoute: ActivatedRoute,
    private formBuilder: FormBuilder, private _createService: SaveService) {

  }

  ngOnInit() {
    this.questionCode = this._activeRoute.snapshot.params['questionType'];

    let data = this._activeRoute.snapshot.data['questionDetail'];
    if (data)
      this.getDataEditForm(data);

  }

  getDataEditForm(data: HighlightIncorrectWord) {
    this.questionId = data.questionId;
    this.createIncorrectWordForm =this.formBuilder.group(
      {
        title: [data.title, [Validators.required]],
        duration: [data.duration, [Validators.required]],
        paragraph: [data.paragraph, [Validators.required]],
        audioFile: [null],
      }
    );

  }

  get f() {
    return this.createIncorrectWordForm.controls;
  }



  goBack() {
    this._createService.goBackPrev();
  }

  onCreate() {
    this.submitted = true;
    if (this.createIncorrectWordForm.invalid) {
      return
    }else{
      this.subscription == this._createService.onSaveHighlightIncorrectWord(this.createIncorrectWordForm.value, this.questionCode,this.questionId).subscribe(
        (res) => {
          this.goBack();
        })
    }
 
  }

  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }
}
