import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SaveService } from '@app/core/http/serviceIndex';
import { FIBDrag } from '@app/shared/models/ReadingModel';
import { Subscription } from 'rxjs';

@Component({
  selector: 'fib-bank',
  templateUrl: './fib-bank.component.html',
  styleUrls: ['./fib-bank.component.css']
})
export class FibBankComponent implements OnInit {
  private questionCode: string;
  private questionId: string;

  // boolean
  public submitted: boolean = false;
  public isToast: boolean = false;

  dragForm: FormGroup = this.formBuilder.group(
    {
      name: ["", [Validators.required]],
      paragraph: ["", Validators.required],
      options: ["", [Validators.required]]
    }
  );

  // any
  public editorData: any;
  public isProgress: boolean = false;
  public warmingTxt: any = "(GIVE BLANKS WITH {WORD} IN THE PLACE YOU WANT IT TO BE BLANK)";
  public subscription: Subscription;

  constructor(private _activeRoute: ActivatedRoute, private _createService: SaveService,
    private formBuilder: FormBuilder) { }

  ngOnInit() {
    this.questionCode = this._activeRoute.snapshot.params['questionType'];

    let data = this._activeRoute.snapshot.data['questionDetail'];
    if (data)
      this.getDataEditForm(data);

  }

  getDataEditForm(data: FIBDrag) {
    this.questionId = data.dragId;
    this.dragForm = this.formBuilder.group(
      {
        name: [data.name, [Validators.required]],
        paragraph: [data.paragraph],
        options: [data.options, [Validators.required]]
      }
    );
    this.editorData = data.paragraph;
  }

  get f() {
    return this.dragForm.controls;
  }

  getMarkDown(e) {
    this.editorData = e;
  }

  onCreate() {
    this.submitted = true;
    if (this.dragForm.invalid && !this.editorData.innerHTML) {
      return;
    } else {
      this.isProgress = true;
      this.dragForm.value.paragraph = this.editorData.innerHTML ? this.editorData.innerHTML : this.editorData;
      this.subscription = this._createService.onSaveDrag(this.dragForm.value, this.questionCode, this.questionId).subscribe(
        (res) => {
          this.isProgress = false;
          this.goBack();
        }
      )
    }
  }

  goBack() {
    this._createService.goBackPrev();
  }



  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }

}
