import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import {  SaveService } from '@app/core/http/serviceIndex';
import { MCQ } from '@app/shared/models/ListeningModel';
import { Subscription } from 'rxjs';

@Component({
  selector: 'mcq-single-listening',
  templateUrl: './mcq-single.component.html',
  styleUrls: ['./mcq-single.component.css']
})

export class McqSingleListeningComponent implements OnInit {
  private questionCode: string;
  private questionId:string;

  public creatMCQForm: FormGroup = this.formBuilder.group({
    title: ["", [Validators.required]],
    duration: ["", [Validators.required]],
    question: ["", [Validators.required]],
    answers: ["", [Validators.required]],
    options: ["", [Validators.required]],
    audioFile:[null,Validators.required]
  });


  // boolean
  public submitted: boolean = false;

  public subscription: Subscription;

  constructor(
    private formBuilder: FormBuilder,
    private _activeRoute: ActivatedRoute,
    private _createService: SaveService
  ) { }

  ngOnInit() {
    this.questionCode = this._activeRoute.snapshot.params['questionType'];

    let data = this._activeRoute.snapshot.data['questionDetail'];
    if (data)
      this.getDataEditForm(data);

  }

  getDataEditForm(data: MCQ) {
    this.questionId = data.questionId;
    this.creatMCQForm= this.formBuilder.group({
      title: [data.title, [Validators.required]],
      duration: [data.duration, [Validators.required]],
      question: [data.question, [Validators.required]],
      answers: [data.answers, [Validators.required]],
      options: [data.options, [Validators.required]],
      audioFile:[null]
    });

  }


  onCreate() {
    this.submitted = true;

    if (this.creatMCQForm.invalid) {
      return;
    } else {
      this.subscription = this._createService.onSaveMCQSingle(this.creatMCQForm.value, this.questionCode,this.questionId).subscribe(
        () => {
          this.goBack();
        });
    }
  }


  goBack() {
    this._createService.goBackPrev();
  }

  get f() {
    return this.creatMCQForm.controls;
  }

  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }
}
