import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SaveService } from '@app/core/http/serviceIndex';
import { DropDown } from '@app/shared/models/ReadingModel';
import { Subscription } from 'rxjs';

@Component({
  selector: 'fib-drop-down',
  templateUrl: './fib-drop-down.component.html',
  styleUrls: ['./fib-drop-down.component.css']
})
export class FibDropDownComponent implements OnInit {
  private questionCode: string;
  private questionId:string;

  // boolean
  public submitted: boolean = false;
  public isToast: boolean = false;
  public isProgress: boolean = false;

  public dropForm: FormGroup = this.formBuilder.group(
    {
      name: ["", [Validators.required]],
      paragraph: ["",Validators.required]
    }
  );

  // any
  public editorData: any = "";
  public isErrText: string;
  public warmingTxt: any = "(GIVE OPTIONS IN {}  SEPARETED BY ; EX: {ANSWER;OPTION1;OPTION2;OPTION3})";
  public subscription: Subscription;

  constructor(private _activeRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private _createService: SaveService,) { }

    ngOnInit() {
      this.questionCode = this._activeRoute.snapshot.params['questionType'];
      let data = this._activeRoute.snapshot.data['questionDetail'];
      if (data)
        this.getDataEditForm(data);
    }
  
    getDataEditForm(data: DropDown) {
      this.questionId = data.dropdownId;
      this.dropForm = this.formBuilder.group(
        {
          name: [data.name, [Validators.required]],
          paragraph: [data.paragraph, [Validators.required]]
        }
      );
        this.editorData=data.paragraph;
    }

  get f() {
    return this.dropForm.controls;
  }

  getMarkDown(e) {
    this.editorData = e;
    this.dropForm.get('paragraph').setValue(e.innerHTML);
  }

  goBack() {
    this._createService.goBackPrev();
  }

  onCreate() {
    this.submitted = true;
    if (this.dropForm.invalid && !this.editorData.innerHTML) {
      return;
    } else {
      this.isProgress = true;

      this.subscription = this._createService.onSaveropDown(
        this.dropForm.value, this.questionCode,this.questionId
      ).subscribe(
        (res) => {
          this.isProgress = false;
          this.goBack();
        }, (err) => {
          this.isErrText = err;
          this.isToast = true;
          this.isProgress = false;
          if (this.editorData.innerHTML) {
            this.editorData = this.editorData.innerHTML;
          } else this.editorData = this.editorData;
        }
      )
    }
  }


  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }
}
