import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import {  SaveService } from '@app/core/http/serviceIndex';
import { Reorder } from '@app/shared/models/ReadingModel';
import { Subscription } from 'rxjs';

@Component({
  selector: 'reorder-paragraph',
  templateUrl: './reorder-paragraph.component.html',
  styleUrls: ['./reorder-paragraph.component.css']
})
export class ReorderParagraphComponent implements OnInit {
  public questionCode: string;
  private questionId: string;

  // boolean
  public submitted: boolean = false;
  public isHint: boolean = false;
  createReorderForm: FormGroup = this.formBuilder.group(
    {
      name: ["", [Validators.required]],
      paragraph: ["", [Validators.required]]
    }
  );

  // any
  public reorderId: any;
  public editorData: any = "";
  public isProgress: boolean = false;
  public subscription: Subscription;

  constructor(private _activeRoute: ActivatedRoute,
    private formBuilder: FormBuilder, private _createService: SaveService,) {

  }

  ngOnInit() {
    this.questionCode = this._activeRoute.snapshot.params['questionType'];
    let data = this._activeRoute.snapshot.data['questionDetail'];
    if (data)
      this.getDataEditForm(data);
  }

  getDataEditForm(data: Reorder) {
    this.questionId = data.reorderId;
    this.createReorderForm = this.formBuilder.group(
      {
        name: [data.name, [Validators.required]],
        paragraph: [data.paragraph, [Validators.required]]
      }
    );
      this.editorData=data.paragraph;
  }

  get f() {
    return this.createReorderForm.controls;
  }

  getMarkDown(e) {
    this.editorData = e;
    this.createReorderForm.get('paragraph').setValue(e.innerHTML);
  }

  goBack() {
   this._createService.goBackPrev();
  }

  onCreate() {
    this.submitted = true;
    if (this.createReorderForm.invalid && !this.editorData.innerHTML) {
      return;
    } else {
      this.isProgress = true;

      this.subscription = this._createService.onSaveReorder(this.createReorderForm.value, this.questionCode,this.questionId).subscribe(
        (res) => {
          this.isProgress = false;
          this.goBack();
        })
    }
  }

 

  clickClose(e) {
    this.isHint = !this.isHint;

  }

  btnHint() {
    this.submitted = true;
    if (this.createReorderForm.invalid) {
      return;
    } else {
      this.isHint = !this.isHint;
      //  let arr = this._service.strToArrbySemicolon(this.createReorderForm.value.paragraph);
      //  this.editorData = this._service.formatarrtoHTML(arr);
    }
  }
}
