import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import {  SaveService } from '@app/core/http/serviceIndex';
import { ToastService } from '@app/core/services';
import { EssaySubWriting, EssayWriting } from '@app/shared/models/WritingModel';
import { Subscription } from 'rxjs';

@Component({
  selector: 'essay-writing',
  templateUrl: './essay-writing.component.html',
  styleUrls: ['./essay-writing.component.css']
})
export class EssayWritingComponent implements OnInit {
  private questionCode: string;
  private questionId: string;

  createEssayForm: FormGroup = this.formBuilder.group(
    {
      name: ["", [Validators.required]],
      answer: this.formBuilder.group({
        contentKeywords: ["", [Validators.required]],
        maxWordLimit: ["", [Validators.required]],
        maxKeyword: ["", [Validators.required]],
      }),
      adviseContent: ["", Validators.required]
    }
  );

  // boolean
  public submitted: boolean = false;
  // any
  public editorData: any = "";
  public isProgress: boolean = false;
  public subscription: Subscription;

  constructor(private _activeRoute: ActivatedRoute,
    private formBuilder: FormBuilder, 
    public toastService: ToastService,
    private _saveService: SaveService) {

  }

  ngOnInit() {
    this.questionCode = this._activeRoute.snapshot.params['questionType'];
    let data = this._activeRoute.snapshot.data['questionDetail'];
    if (data)
      this.getDataEditForm(data);
  }

  getDataEditForm(data: EssayWriting) {
    this.questionId = data.essayId;
    this.createEssayForm = this.formBuilder.group(
      {
        name: [data.name, [Validators.required]],
        answer: this.formBuilder.group({
          contentKeywords: [data.answer.contentKeywords, [Validators.required]],
          maxWordLimit: [data.answer.maxWordLimit, [Validators.required]],
          maxKeyword: [data.answer.maxKeyword, [Validators.required]],
        }),
       
        adviseContent: [data.adviseContent]
      }
    );
    this.editorData = data.adviseContent;
  }

  onCreate() {
    this.submitted = true;
    if (this.createEssayForm.invalid) {
      return;
    } else {
      this.isProgress = true;

      this.createEssayForm.value.advis
      this.subscription = this._saveService
        .onSaveEssay(this.createEssayForm.value, this.questionCode, this.questionId)
        .subscribe((res) => {
          this.isProgress = false;
          this.goBack();
        });
    }
  }


  get f() {
    return this.createEssayForm.controls;
  }

  getMarkDown(e) {
    this.editorData = e;
    this.createEssayForm.get('adviseContent').setValue(e.innerHTML);
  }

  goBack() {
    this._saveService.goBackPrev();
  }


  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }

}
