import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { DetailService } from '@app/core/http/detail.service';

@Injectable()
export class QuestionDetailResolver implements Resolve<any> {

    constructor(private question: DetailService) {
    }

    resolve(route: ActivatedRouteSnapshot):
        Observable<any> {

        const questionUrl = `${route.params['moduleType'].toLowerCase()}/${route.params['questionType']}/${route.params['questionId']}/detail`;

        return this.question.getDetail(questionUrl);
    }
}