import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CommonService } from '@app/core/http/common.service';

@Component({
  selector: 'app-question-create',
  templateUrl: './question-create.component.html',
  styleUrls: ['./question-create.component.css']
})
export class QuestionCreateComponent implements OnInit {
  public currentModule:any={};
  public questionName:string;


  constructor(private _activeRoute:ActivatedRoute,private _cservice:CommonService) { }

  ngOnInit() {
    this.currentModule=this._activeRoute.snapshot.params;

    this.questionName=this._cservice.getquestionName(this.currentModule.questionType);
  }

}
