import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SaveService } from '@app/core/http/serviceIndex';
import { FIB } from '@app/shared/models/ListeningModel';
import { Subscription } from 'rxjs';

@Component({
  selector: 'fib',
  templateUrl: './fib.component.html',
  styleUrls: ['./fib.component.css']
})
export class FibComponent implements OnInit {

  private questionCode: string;
  private questionId: string;

  public creatFIBForm: FormGroup = this.formBuilder.group({
    title: ["", [Validators.required]],
    duration: ["", [Validators.required]],
    paragraph: ["", [Validators.required]],
    audioFile: [null, [Validators.required]],
  });

  public isErrText: string = "You must upload audio file";

  // boolean
  public submitted: boolean = false;
  isToast: boolean = false;
  public answerWarningText: string =
    '(GIVE ANSWER IN {} EX; "THIS IS A {PARAGRAPH}.IT HAS .. ")';

  private subscription: Subscription;

  constructor(
    private _createService: SaveService,
    private _activeRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
  ) { }


  ngOnInit() {
    this.questionCode = this._activeRoute.snapshot.params['questionType'];

    let data = this._activeRoute.snapshot.data['questionDetail'];
    if (data)
      this.getDataEditForm(data);

  }

  getDataEditForm(data: FIB) {
    this.questionId = data.questionId;
    this.creatFIBForm = this.formBuilder.group({
      title: [data.title, [Validators.required]],
      duration: [data.duration, [Validators.required]],
      paragraph: [data.paragraph, [Validators.required]],
      audioFile: [null],
    });

  }

  get f() {
    return this.creatFIBForm.controls;
  }



  goBack() {
    this._createService.goBackPrev();
  }

  onCreate() {
    this.submitted = true;
    if (this.creatFIBForm.invalid) {
      return;
    } else {

      this._createService.onSaveFIB(this.creatFIBForm.value, this.questionCode, this.questionId).subscribe(
        (res) => {
          this.goBack();
        });
    }
  }

  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }

}
