import { Component, OnInit, Input} from '@angular/core';
import { FormArray, FormBuilder, Validators } from '@angular/forms';
import { Word } from '@app/shared/models/SpeakingModel';

@Component({
  selector: 'word-checker',
  templateUrl: './word-checker.component.html',
  styleUrls: ['./word-checker.component.css']
})
export class WordCheckerComponent implements OnInit {
  public dialog:any={
    showText:"",
    isDelete:false,
    isWarning:true,
    isconfirmBtn:true
  }

  @Input() wordGroup
  @Input() submitted
  @Input() wordsArray:Array<Word>;
  @Input() isCreate
  control: FormArray
  isDialog: boolean = false

  constructor(private fb: FormBuilder) { }

  ngOnInit() { 
    if (this.isCreate) {
      const control = this.wordGroup.get('words') as FormArray
      control.push(this.initiateForm(null))
    } else {
      setTimeout(() => {     
        this.wordsArray.forEach(element => {
          const control = this.wordGroup.get('words') as FormArray
          control.push(this.initiateForm(element))
        })
      }, 2000);    
    }
  }

  ngAfterOnInit() {
    //this.control = this.wordGroup.get('rows') as FormArray
  }

  initiateForm(data) {
    return this.fb.group({
      word: [ data ? data.word : '', [Validators.required]],
      isDeleteAble: [true]
    })
  } 

  onAdd(){
    const control = this.wordGroup.get('words') as FormArray
    control.push(this.initiateForm(null));
  }

  onDelete(index: number){
    const control =  this.wordGroup.get('words') as FormArray
    if(control.controls.length < 2){
      this.isDialog = true
      this.dialog.showText = "Must have one row at least in Words To Check With Minimum."
      return
    }
     
    control.removeAt(index)
  }

  get getFormControls() {
    const control = this.wordGroup.get('words') as FormArray;
    return control
  }

  onSubmit(e) {
    this.isDialog = false
  }

}
